
@font-face {
  font-family: 'icon';
  /* project id 277165 */
  src: url("//at.alicdn.com/t/font_kmywdojzhchj8aor.eot");
  src: url("//at.alicdn.com/t/font_kmywdojzhchj8aor.eot?#iefix") format("embedded-opentype"), url("//at.alicdn.com/t/font_kmywdojzhchj8aor.woff") format("woff"), url("//at.alicdn.com/t/font_kmywdojzhchj8aor.ttf") format("truetype"), url("//at.alicdn.com/t/font_kmywdojzhchj8aor.svg#iconfont") format("svg");
}
.icon {
  font-family: "icon" !important;
  font-size: 18px;
  font-style: normal;
  color: #ffffff;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  background: rgba(8, 5, 58, 0.9);
}
#app .play-loading {
    top: 8rem;
    z-index: 99;
}
#app .container {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    overflow: auto;
    overflow-x: hidden;
}
#app .slide-left-enter-active {
    -webkit-animation: slideLeft .3s;
            animation: slideLeft .3s;
}
#app .slide-right-enter-active {
    -webkit-animation: slideRight .3s;
            animation: slideRight .3s;
}
#app .fold-enter-active,
  #app .fold-leave-active {
    transition: -webkit-transform .3s ease-in;
    transition: transform .3s ease-in;
    transition: transform .3s ease-in, -webkit-transform .3s ease-in;
}
#app .fold-enter,
  #app .fold-leave-active {
    -webkit-transform: translate3d(0, 100%, 0);
            transform: translate3d(0, 100%, 0);
}
@-webkit-keyframes slideLeft {
from {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes slideLeft {
from {
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes slideRight {
from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@keyframes slideRight {
from {
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
    visibility: visible;
}
to {
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}



